  box-shadow: -2px 4px 17px 0px #000;
  transition: all 0.5s;
  z-index: 100;
}
.rightside-menu.rightside-menu--close {
  transform: translateX(500%);
}
.rightside-menu__content {
  text-align: center;
  max-width: 490px;
  margin: 0 auto;
}
.rightside-menu__close {
  background-color: transparent;
  border: none;
  outline: none;
  padding: 0;
  cursor: pointer;
  margin-left: 50px;
  margin-bottom: 60px;
  font-size: 20px;
  font-weight: 400;
}
.rightside-menu__logo {
  display: inline-block;
}
.rightside-menu__title {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  line-height: 50px;
  color: #363638;
