.collection {
  display: grid;
  margin-top: 100px;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
}
.collection__item {
  position: relative;
  cursor: pointer;
}
.collection__item:hover .collection__info {
  opacity: 1;
}
.collection__images {
  object-fit: cover;
  height: 400px;
  width: 400px;
}
.collection__info {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: all 0.5s;
  opacity: 0;
}
.collection__info-title {
  font-size: 20px;
  color: #363638;
  margin-bottom: 19px;
  padding-bottom: 19px;
  position: relative;
}
.collection__info-title::after {
  content: "";
  left: 0;
  right: 0;
  bottom: 0;
  height: 1px;
  position: absolute;
  background-color: #6D7278;
  margin: 0 auto;
  width: 200px;
}
.collection__info-text {
  font-family: "Open Sans";
  font-weight: 600;
  font-size: 14px;
  color: #707070;
}

